.input {
  width: 180px;
  margin: 10px auto;
}
.input input {
  width: 100%;
  padding: 5px;
  border: 2px solid blue;
  border-radius: 10px;
  box-sizing: border-box;
  outline: none;
}

.input input:focus {
  border: 2px solid blue;
  border-radius: 10px;
}

.error {
  width: max-content;
  margin: 0 auto;
  color: red;
}
