.weatherWrapper {
  background-color: rgb(255, 255, 255);
  border-radius: 25px;
}
.date {
  width: max-content;
  margin: 10px auto;
  font-size: 20px;
}
.cityName {
  width: max-content;
  margin: 10px auto;
  font-size: 26px;
  font-weight: bold;
}

.temperature {
  width: max-content;
  margin: 0 auto;
  font-size: 37px;
}

.temperatureDifference {
  width: max-content;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.temperatureMin,
.temperatureMax {
  padding: 5px;
  font-size: 20px;
}
.temperatureMin img,
.temperatureMax img {
  width: 18px;
  height: 25px;
}

.weatherDescription {
  width: max-content;
  margin: 40px auto;
  font-size: 20px;
}
.weatherButton {
  margin: 0 auto;
  width: 50px;
  color: rgb(255, 255, 255);
  padding: 2px;
  border-radius: 10px;
  background-color: rgb(58, 89, 156);
  text-align: center;
  cursor: pointer;
}
.weatherButton:hover {
  background-color: rgb(137, 55, 231);
  color: yellow;
}
.weather {
  width: 300px;
  height: 400px;
  background-color: rgb(255, 255, 255);
  border-radius: 25px;
  position: relative;
  perspective: 1000px;
}
.weatherFront,
.weatherBack {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition: 1s;
  backface-visibility: hidden;
}

.weatherBackNoActive {
  transform: rotateY(-180deg);
}
.weatherBackActive {
  transform: rotateY(-360deg);
}
.weatherFrontActive {
  transform: rotateY(-180deg);
}

.wetherBackItem {
  width: max-content;
  margin: 30px auto;
  font-size: 24px;
}
